<script setup>
	import { ref, defineEmits } from "vue";
	import axios from "axios";

	// 1. O prop 'multiple' não é mais necessário, então foi removido.

	// 2. O estado agora armazena um único arquivo (ou null), em vez de um array.
	const file = ref(null);
	const type = ref(2);
	const isLoading = ref(false);

	const emit = defineEmits(["changed"]);

	function handleFileSelect(e) {
		const input = e.target;
		// 3. Pegamos apenas o primeiro arquivo da lista (índice 0).
		const selectedFile = input?.files[0];
		if (selectedFile) {
			file.value = selectedFile;
			emit("changed", file.value);
		}
	}

	// 4. A função de remoção agora simplesmente limpa o estado.
	function removeFile() {
		file.value = null;
		emit("changed", null);
	}

	async function handleUpload() {
		// A verificação agora é se 'file.value' existe.
		if (!file.value) {
			alert("Por favor, selecione um arquivo.");
			return;
		}

		const formData = new FormData();

		// 5. Adicionamos o único arquivo ao FormData.
		//    É uma boa prática usar uma chave no singular ('documento') se é apenas um arquivo.
		//    Verifique se seu backend espera 'documento' ou 'documentos'.
		formData.append("documento", file.value);
		formData.append("tipo", type.value);

		isLoading.value = true;

		try {
			const response = await axios.post("/aluno/documento", formData, {
				headers: {
					"Content-Type": "multipart/form-data",
				},
			});

			console.log("Arquivo enviado com sucesso:", response.data);
			alert("Arquivo enviado com sucesso!");
			file.value = null; // Limpa o arquivo após o envio
			emit("changed", null);
		} catch (error) {
			console.error("Erro ao enviar o arquivo:", error);
			alert("Ocorreu um erro ao enviar o arquivo. Tente novamente.");
		} finally {
			isLoading.value = false;
		}
	}
</script>

<template>
	<div>
		<label
			for="file-input"
			class="btn btn-primary"
			>Adicionar Envio</label
		>
		<input
			id="file-input"
			type="file"
			@change="handleFileSelect"
			hidden
			ref="fileInput"
		/>

		<div
			v-if="file"
			class="file-info"
		>
			<span>{{ file.name }}</span>
			<button
				@click="removeFile"
				:disabled="isLoading"
				title="Remover"
			>
				&times;
			</button>
		</div>
		<p v-else>Nenhum arquivo selecionado.</p>

		<button
			v-if="file"
			@click="handleUpload"
			class="btn btn-success"
			:disabled="isLoading"
		>
			{{ isLoading ? "Enviando..." : "Enviar Arquivo" }}
		</button>
	</div>
</template>

<style scoped>
	/* Estilos podem ser mantidos ou ajustados */
	.btn {
		padding: 8px 12px;
		border: none;
		border-radius: 4px;
		cursor: pointer;
		margin-right: 10px;
	}
	.btn-primary {
		background-color: #007bff;
		color: white;
	}
	.btn-success {
		background-color: #28a745;
		color: white;
		margin-top: 10px;
	}
	.file-info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 8px;
		border: 1px solid #ccc;
		border-radius: 4px;
		margin-top: 15px;
	}
	.file-info button {
		background: none;
		border: none;
		font-size: 1.2rem;
		color: red;
		font-weight: bold;
		cursor: pointer;
	}
</style>
