<script>
export default {
  props: {
    show: Boolean,
    id: Number,
    operation: String,
  },
}
</script>

<template>
  <Transition name="modal">
    <div v-if="show" class="modal-mask">
      <div class="modal-container">
        <div class="modal-header">
          <slot name="header"></slot>
        </div>

        <div class="modal-body">
          <slot name="body">
            
          </slot>
        </div>

        <div class="modal-footer">
          <slot name="footer">
            <button
              class="modal-button mx-1 btn btn-outline-success opacity-75"
              @click="$emit(operation, id)"
            >{{ $t('button.yes') }}</button>

            <button
              class="modal-button mx-1 btn btn-outline-danger opacity-75"
              @click="$emit('close')"
            >{{ $t('button.no') }}</button>
          </slot>
        </div>
      </div>
    </div>
  </Transition>

</template>

<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  transition: opacity 0.3s ease;
}

.modal-container {
  width: 500px;
  margin: auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
}

.modal-body {
  padding: 20px 0px 40px 0px;
}

.modal-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter-from {
  opacity: 0;
}

.modal-leave-to {
  opacity: 0;
}

.modal-enter-from .modal-container,
.modal-leave-to .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>